import React from "react"
import styled from "styled-components"
import { CountdownConsumer } from "./CountdownContext"
import CountdownItem from "./CountdownItem"

const Container = styled.div.attrs({ className: "flex w100 jc-c fxw-w" })`
  margin-top: 10px;
`

const Countdown = () => (
  <CountdownConsumer>
    {countdown => (
      <Container>
        <CountdownItem text="Jours" time={countdown.days} />
        <CountdownItem text="Heures" time={countdown.hours} />
        <CountdownItem text="Minutes" time={countdown.minutes} />
        <CountdownItem text="Secondes" time={countdown.seconds} />
      </Container>
    )}
  </CountdownConsumer>
)

export default Countdown
